* {
  padding: 0;
  margin: 0;
  text-decoration: none;
  list-style: none;
  box-sizing: border-box;
}

.home {
  position: relative;
  width: 100%;
  height: calc(115vh - 50px);
}

.contenido {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 90%;
  color: #ffff;
  text-align: center;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-content: center;
}

.contenido h1 {
  margin: 15px;
  font-size: px;
  border: none;
}

.contenido p {
  margin: 20px;
  font-size: 18px;
}

.contenido .button {
  color: #4ab;
  padding: 9px 20px;
  text-transform: uppercase;
  border: 1px solid #4ab;
  transition: all 0.3s ease-in-out;
  width: 150px;
}
.contenido .button:hover {
  background: #4ab;
  color: #000;
}

.icon {
  margin: 0;
}
