
.proyectos-reciente {
    padding: 40px;
    margin-bottom: 2.5rem;
  }
.proyectos-reciente h2{
    display: flex;
    justify-content: center;
}

.proyectos-reciente h3{
    display: flex;
    justify-content: center;
}

.proyecto img {
    height: 100%;
    width: 100%;
    margin-top: 50px;
    padding: 10px;
    border-radius: 10px;
    display: block;
    transition: all 0.2s ease;
  }
  
.proyectos-contenedor {
  padding-top: 60px;
  margin-bottom: 40px;
  }
  
  .overlay {
    transition: all 0.2s ease;
    opacity: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
  }
  
  .overlay p {
    font-size: 25px;
    font-weight: bold;
    margin-bottom: 0;
  }
  
  .proyecto {
    position: relative;
  }
  
  .proyecto:hover img {
    opacity: 0.2;
  }
  
  .proyecto:hover .overlay {
    opacity: 1;
  }
  
  .overlay .iconos-contenedor {
    display: flex;
  }
  
  .overlay i {
    color: black;
    font-size: 60px;
    margin: 10px;
  }
  